          <label class="col-lg-4 control-label" style="color: red;" v-if="limitDateState === 1">1{{limitDate}}</label>
          <button type="button" class="btn btn-success " v-if="pojs.length > 0" @click="submit" :disabled="limitDateState === 1 || isScored ==='Y'" ></button>
          <button type="button" class="btn btn-info" @click="goBack"></button>
        </div>
